
import { Component, ViewChild } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Chart } from 'chart.js';

function getTime() {
    return (new Date()).getTime()/1000.0;
}

class SlidingWindow
{
    private values: any;
    private windowLength: number;
    
    constructor(length) {
        this.values = [];
        this.windowLength = length;
    }

    get start() {
        return getTime() - this.windowLength;
    }

    add(timestamp)
    {
        let start = this.start;
        this.values.push(timestamp)
        this.values = this.values.filter(
            value => value >= start
        );
    }

    getAveragePeriod()
    {
        if (this.values.length <= 1) {
            return null;
        }
        
        let sum = 0;
        for (let n = 1; n < this.values.length; n++) {
            sum += this.values[n]-this.values[n-1];
        }
        return sum / (this.values.length-1);
    }
}

@Component({
    selector: 'page-home',
    templateUrl: 'home.html'
})
export class HomePage
{
    private beatsPerMinute: number = 123;
    private window: SlidingWindow = null;
    @ViewChild('chart') chartCanvas;
    private chart: Chart = null;

    constructor(public navCtrl: NavController)
    {
        this.window = new SlidingWindow(5);
    }

    ionViewDidLoad()
    {
        this.chart = new Chart(this.chartCanvas.nativeElement, {
            type: 'bar',
            data: {
                labels: ['Red', 'Blue', 'Yellow'],
                datasets: [{
                    label: 'Some label',
                    data: [1, 2, 3],
                }],
            },
        });

        document.addEventListener(
            'keydown',
            key => {
                this.handleClicked();
            }
        );
    }

    handleClicked()
    {
        this.window.add(getTime());

        let bpm = (1000*this.window.getAveragePeriod());
        this.beatsPerMinute = bpm|0;
    }

    handleKeyPress() {
        console.log('key');
    }

}



